import { Component } from 'react';
import { Modal } from 'react-bootstrap';

class editPerson extends Component {

  state = {
    nombre: "",
    edad: "",
    id: "",
    imagen: "",


  }

  componentDidMount(){
    console.log("COMPONENTE MONTADO");
    this.setState({
      nombre: this.props.data.nombre,
      edad: this.props.data.edad,
      id: this.props.data.id,
      imagen: this.props.data.imagen
    }, () => console.log(this.state))
  }

  refName = (ref) => {

    this.setState({ nombre: ref.target.value});

  }

  refEdad = (ref) => {

    this.setState({ edad: ref.target.value });
  }


  refId = (ref) => {

    this.setState({ id: ref.target.value });
  }

  refImagen = (ref) => {

    this.setState({ imagen: ref.target.value });
  }


  putName = () => {
    console.log(this.state.nombre);
  }

  onHide = () => {
    this.setState({ show:this.props.hidden })
  }

  handleSubmit = (event) => {
    event.preventDefault();
    this.props.hidden();

    console.log(this.state);
    this.sendData();
  }

  sendData = () => {
    // console.log(this.state);
    this.props.dataActualizada(this.state);


  }

  render(){

    return(
      <>


        <Modal show={this.props.show} onHide={this.props.hidden}>

          <Modal.Header closeButton>
            <Modal.Title>Modal heading</Modal.Title>
          </Modal.Header>
          <form onSubmit={this.handleSubmit}>
            <Modal.Body>Woohoo, you're reading this text in a modal!
            <span className="input-group-text" id="addon-wrapping">Nombre</span>
              <input type="text" className="form-control" placeholder="Ingresar Nombre" value={this.state.nombre} onChange={this.refName} />

              <br />

              <span className="input-group-text" id="addon-wrapping">Edad</span>
              <input type="text" className="form-control" placeholder="Ingresar Edad" defaultValue={this.state.edad} onChange={this.refEdad}/>

              <br />



              <br />

              <span className="input-group-text" id="addon-wrapping">ID</span>
              <input type="text" className="form-control" placeholder="Ingresar cargo" defaultValue={this.state.id} onChange={this.refId}/>

              <br />

              <span className="input-group-text" id="addon-wrapping">Imagen</span>
              <input type="text" className="form-control" placeholder="Ingresar Imagen" defaultValue={this.state.imagen} onChange={this.refImagen}/>

            </Modal.Body>

            <Modal.Footer>

              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal" onClick={this.props.hidden}>Close</button>
              <button type="submit" className="btn btn-primary">Save changes</button>
            </Modal.Footer>
          </form>

        </Modal>
      </>
    )
  }
}

export default editPerson;