import { React, Component } from 'react';
import './style.css';
//import Boton from '../boton/boton';
import Select from '../select/select';
import Popup from '../popup/popup';

// // import Popup from '../popup/Popup';

class Cajas extends Component {

    state = {
        mensaje: "Hola",
        mostrarDetalle: false,
        nuevoColor: "white",
        textColor: "White",
        textInput: ["Manzana","Pera"],
        popupMostrar: false,

    }

    showText = (text) => {
        console.log(text);
        var arrFrutas = this.state.textInput;
        arrFrutas.push(text);
        console.log(arrFrutas);

        this.setState({
            textInput:arrFrutas,
            popupMostrar: false,

        });
        //cambio el estado a false porque lo envio en la prop change envio el estadp popupMostrar
    }

    cambiarMensaje = () => {
        this.setState({ mensaje: "Nuevo mensaje" })
    }

    mostrarDetalles = (persona) => {

        this.setState({mostrarDetalle: true})
        /*if (hidden === "block") {
            document.querySelector(".box").style.display = "none";
        }*/
    }

    showimgPopup = () => {
        this.setState({popupMostrar: true});
    }

    hiddenPop = () => {
        this.setState({popupMostrar: false})
        //asi ocultaba antes el pop up enmbviandolo como propiedad
    }

    ocultarDetalles = () => {
        this.setState({mostrarDetalle: false})
    }

    changeBackground = (color) => {
        console.log(color.target.value);
        this.setState({nuevoColor: color.target.value});

        if (color.target.value === "red") {
            this.setState({textColor: "blue"})
        }else if(color.target.value === "green") {
            this.setState({textColor: "red"})
        }else {
            this.setState({textColor: "black"})
        }
    }

    eliminarCard = (nombre) => {
        console.log(nombre);
    }


    render() {

        return (
            < div className="box" style={{ marginLeft: "40px", display: "block", backgroundColor: this.state.nuevoColor, color: this.state.textColor}}>
                {/* EN el style son dos llaves porque la primera es para el estilo y la segunda es de React */}
                {/*<p>Mi Nombre es {this.props.name}</p>
                    <p>Mi Edad es {this.props.edad}</p>*/}
                {/* Cada vez que cambia el background de card cambiar el color del texto dependiendo del background */}
                <a href="#" type="button" data-bs-toggle="modal" data-bs-target="#exampleModal" onClick={this.showimgPopup}>
                    <img src={this.props.persona.imagen} style={{ cursor: "pointer" }} width="100px"></img>
                </a>

                {/* Crear una funcion que me permita interactuar con el input ubicado en el modal para practicar de hijo a padre
                al dar click en guardar, mostrar el mensaje escrito en la caja debajo del ocultar */}

                <br />
                <p className="alert alert-info py-0">Mi Nombre es {this.props.persona.nombre}</p>
                {/* Cuando le ponga mostrar detalles que muestre un combo box con las siguientes opciones que permita
                cambiar el color de fondo de la tarjeta.

                Agregar evento a la imagen para que cuando pase el mouse el cursor cam,bie a mano y pueda darle click a la imagen
                esto debera mostrar una alerta con un texto
                */}

                {
                    this.state.mostrarDetalle === true &&
                    //esto lo muestra del mostrar detalle
                    //&& siempre al final de una condicional el &&
                    //puedes poner === false o usar de manera implista !
                    //! significa que es diferente de en este caso true
                    <div>
                        {/* <Select></Select> */}

                        {/* <select id="select" className="form-select" aria-label="Default select example" onChange={(color) => this.changeBackground(color)}>
                            <option selected>Escoje un Color</option>
                            <option value="red">Rojo</option>
                            <option value="green">Verde</option>
                            <option value="yellow">Amarillo</option>
                        </select> */}
                        <Select funcion={this.changeBackground}></Select>
                        {/* Aca estoy enviando la propiedad funcion a el componente select */}
                        <p><strong>Mi ID es {this.props.persona.id}</strong></p>
                        <p><strong>Mi edad es {this.props.persona.edad}</strong></p>

                    </div>
                }


                 {/* <p>{this.state.mensaje}</p> */}

                {/* <button onClick={this.cambiarMensaje}>Mostrar Texto</button> */}
                {
                    this.state.textInput.map((frutas, index) => <p key={index} className="alert alert-primary">{frutas}</p>)
                }

                <button className="btn btn-info" onClick={this.mostrarDetalles}>Mostrar Detalles</button>

                <button style={{marginTop:"5px"}} className="btn btn-primary" onClick={this.ocultarDetalles}>Ocultar Detalles</button>
                <button className="btn btn-danger" onClick={() => this.props.ridPerson(this.props.persona)}>ELIMINAR</button>
                <button style={{ marginLeft: "7px" }} onClick={() => this.props.modifyPerson(this.props.persona)} className="btn btn-success">EDITAR</button>

                <Popup change={this.showText} estadoPop={this.state.popupMostrar} hidden={this.hiddenPop} ></Popup>



                {/* <Boton persona={this.props.persona}></Boton> */}

            </div >
        )
    }

}

export default Cajas;