import { React, Component } from 'react';
import { Modal } from 'react-bootstrap';

class Modaladdperson extends Component {
  state = {
    estadoEnvio: false,
    show: false,
    nombre: "",
    edad: "",
    id: "",
    imagen: ""
  }

  sendName = (nameRef) => {
    let nameFull = nameRef.target.value;
    this.setState({ nombre: nameFull }, () => console.log(this.state.nombre)); //Aca hago un call back cuando termina el cambio de estado
    //console.log(this.state.nombre);//aqui se ejecuta mientras cambie el estado
  }

  sendEdad = (edadRef) => {
    let edadFull = edadRef.target.value;
    this.setState({ edad: edadFull }, () => console.log(this.state.edad));
    //console.log(this.state.edad);

  }



  sendId = (idRef) => {
    let idFull = idRef.target.value;
    this.setState({ id: idFull }, () => console.log(this.state.id));
  }

  sendImage = (imageRef) => {
    let imageFull = imageRef.target.value;
    this.setState({ imagen: imageFull }, () => console.log(this.state.imagen));
    //console.log(this.state.imagen);
  }

  sendData = () => {
    this.setState({ estadoEnvio: true })
    this.props.newPerson(this.state.nombre,
      this.state.edad,this.state.id, this.state.imagen);
    // this.props.edad(this.state.edad);
    // this.props.cargo(this.state.cargo);
    // this.props.imagen(this.state.imagen);
  }

  handleSubmit = (event) => {
    event.preventDefault();
    this.sendData();
    this.props.hidden();

  }
  render() {
    return (
      <>


        <Modal show={this.props.show} onHide={this.props.hidden}>

          <Modal.Header closeButton>
            <Modal.Title>Modal heading</Modal.Title>
          </Modal.Header>
          <form onSubmit={this.handleSubmit}>
            <Modal.Body>Woohoo, you're reading this text in a modal!
              <span className="input-group-text" id="addon-wrapping">Nombre</span>
              <input type="text" className="form-control" placeholder="Ingresar Nombre" onChange={this.sendName}  />

              <br />

              <span className="input-group-text" id="addon-wrapping">Edad</span>
              <input type="text" className="form-control" placeholder="Ingresar Edad" onChange={this.sendEdad} />

              <br />



              <br />

              <span className="input-group-text" id="addon-wrapping">ID</span>
              <input type="text" className="form-control" placeholder="Ingresar cargo" onChange={this.sendId} />

              <br/>

              <span className="input-group-text" id="addon-wrapping">Imagen</span>
              <input type="text" className="form-control" placeholder="Ingresar Imagen" onChange={this.sendImage} />

            </Modal.Body>

            <Modal.Footer>
              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal" onClick={this.props.hidden}>Close</button>
              <button type="submit" className="btn btn-primary">Save changes</button>
            </Modal.Footer>
          </form>

        </Modal>
      </>
    )
  }
}
export default Modaladdperson;