import { Modal } from 'react-bootstrap';
import React, { Component } from 'react';

class Popup extends Component {
    state = {
        show: false,
        text: ''
    }
    //mostrar popup con la imagen y quitar el boton mostra popup

    changeText = (ref) => {
        this.setState({ text: ref.target.value });
    }

    handleSubmit = (event) => {
        event.preventDefault();
        this.props.change(this.state.text);//llamo esta propiedad para que guarde el text
        console.log("Funciono handlesubmit");
    }

    handleClose = () => {
        this.props.change(this.state.text); //Obtiene el texto del input para enviarlo change en popup en cajas
        //this.setState({ show: false }) //Cierra el popup asi era antes
    };
    handleShow = () => this.setState({ show: true });

    render() {
        return (
            <>


                <Modal show={this.props.estadoPop} onHide={this.handleClose}>

                    <Modal.Header closeButton>
                        <Modal.Title>Modal heading</Modal.Title>
                    </Modal.Header>
                    <form onSubmit={this.handleSubmit}>
                        <Modal.Body>Woohoo, you're reading this text in a modal!
                    <input type="text" className="form-control" placeholder="Nuevo mensaje" onChange={this.changeText} />
                            <p>{this.state.text}</p>
                        </Modal.Body>

                        <Modal.Footer>
                            <button className="btn btn-secondary" onClick={this.props.hidden}>
                                Close
        </button>
                            <button type="submit" className="btn btn-primary" onClick={this.handleClose}>
                                Save Changes
        </button>
                        </Modal.Footer>
                    </form>

                </Modal>
            </>
        )
    }
}
export default Popup;
