import {React, Component} from 'react';
import Cajas from '../props/Cajas';


class Select extends Component {

  state = {
    estadoColor: false,
    color: "white"
  }

  // changeBackground = (color) => {
  //   this.setState({estadoColor: true})
  //   console.log(color.target.value);
  //   this.setState({color: color.target.value});


  // }
  render(){

    return(
      <div className="select">
        <select id="select" className="form-select" aria-label="Default select example" onChange={(color) => this.props.funcion(color)}>
          {/* Con el this.props.funcion recibo la propiedad que envio desde el componente Cajas */}
          <option selected>Escoje un Color</option>
          <option value="red">Rojo</option>
          <option value="green">Verde</option>
          <option value="yellow">Amarillo</option>
        </select>
      </div>


    )
  }
}

export default Select;